import React, { Component } from 'react';
import { connect } from 'react-redux';
import { setRecipe } from '../../action';

import Item from '../Item/Item';

class SearchRecipe extends Component {
	state = {
		ingridients: '',
		name: ''
	};

	find = () => {
		const { ingridients, name } = this.state;
		console.log({
			ingridients,
			name
		});

		const url = 'https://recipepuppyproxy.herokuapp.com/api/';
		
		fetch(`${url}?i=${ingridients}&q=${name}`, {
			method: 'GET'
		})
			.then((res) => res.json())
			.then((json) => {
				let { results } = json;
				this.props.setRecipe(results);
				results = results.map((data) => {
					data.star = false;
					return data;
				});

				this.setState({ results });
			});
	};

	render() {
		return (
			<div>
				<input
					type="text"
					value={this.state.ingridients}
					onChange={(e) => this.setState({ ingridients: e.target.value })}
				/>

				<input type="text" value={this.state.name} onChange={(e) => this.setState({ name: e.target.value })} />

				<button onClick={() => this.find()}> Find </button>

				<div>
					<h2> List </h2>
					{this.props.result.map((result, index) => <Item key={index} result={result} isFav={false} star={result.star}/>)}
				</div>
			</div>
		);
	}
}

const mapToProps = (state) => ({
	result: state.recipe
});

export default connect(mapToProps, {
	setRecipe
})(SearchRecipe);
