import React from 'react';
import { connect } from 'react-redux';
import { addFavroite, delFavroite } from '../../action';

import './Item.css';

class Item extends React.Component {

	fav = (result) => {
		this.props.addFavroite(result);
		
	};

	delFav = (result) => {
		this.props.delFavroite(result);
	}

	render() {
		// console.log('this.props', this.props);
		let { result, isFav, star} = this.props;

		return (
			<div className="Item_Container ">
				<div className="Item_Middle">
					{isFav ? (
						<div className="Star" onClick={() => this.delFav(result)}>
							BlackStar
						</div>
					) : !star ? (
						<div className="Star" onClick={() => this.fav(result)}>
							Star
						</div>
					) : (
						''
					)}

					<a href={result.href}>{result.title}</a>
					<p>{result.ingredients}</p>
				</div>

				<div className="Item_Star"> Img </div>
			</div>
		);
	}
}


export default connect(null, {
	addFavroite,
	delFavroite
})(Item);
